// the map component, utilizes mapbox map api to display the map
import { useState } from "react";
import ReactMapGL from "react-map-gl";
import { map } from "../../data/data";

export default function Map() {
	// config required by mapbox to display map
	const [viewport, setViewport] = useState({
		latitude: map.latitude,
		longitude: map.longitude,
		zoom: 12,
	});

	return (
		// component that displays the map, make sure you have a correct mapbox api acess token for this to work
		<ReactMapGL
			mapStyle="mapbox://styles/mapbox/dark-v10"
			mapboxApiAccessToken={map.mapboxApiAccessToken}
			{...viewport}
			width="100%"
			height="100%"
			onViewportChange={(viewport) => setViewport(viewport)}
		/>
	);
}
