// displays the contact page
// uses emailJs to send emails
// uses mapbox api to display map
import { useEffect } from "react";
import PageTitle from "../../components/utils/PageTitle";
import Map from "../../components/map";
import { contactInfo } from "../../data/data";
import ContactForm from "./ContactForm";

export default function Contact() {
	// update document title and scroll to top on page load
	useEffect(() => {
		document.title = "Contact";
		window.scrollTo({ top: 0, behavior: "smooth" });
	}, []);

	return (
		<div className="animate__animated animate__fadeIn py-20">
			<PageTitle>Contact</PageTitle>
			<div className="sm:flex mb-20 sm:space-x-10">
				<div className="sm:w-1/2 mb-16 sm:mb-0">
					{/* display contact form */}
					<ContactForm />
				</div>
				<div className="sm:w-1/2 relative">
					<div
						className="h-72 sm:h-full relative z-10 bg-gray-100 dark:bg-gray-800"
						style={{ width: "95%" }}>
						{/* display the map */}
						<Map />
					</div>
					<div
						className="bg-primary absolute"
						style={{ width: "60%", height: "80%", right: "0", top: "-5%" }}
					/>
				</div>
			</div>
			<div className="sm:grid sm:grid-cols-2">
				<div className="mb-5 flex space-x-5">
					<div className="h-14 w-14 rounded-full bg-primary text-white flex items-center justify-center flex-shrink-0">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-6 w-6"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor">
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={1.5}
								d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z"
							/>
						</svg>
					</div>
					<div className="flex flex-col justify-center">
						<h5 className="text-gray-800 uppercase dark:text-white mb-0 leading-5">
							Phone
						</h5>
						<span>{contactInfo.phone}</span>
					</div>
				</div>
				<div className="mb-5 flex space-x-5">
					<div className="h-14 w-14 rounded-full bg-primary text-white flex items-center justify-center flex-shrink-0">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-6 w-6"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor">
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={1.5}
								d="M16 12a4 4 0 10-8 0 4 4 0 008 0zm0 0v1.5a2.5 2.5 0 005 0V12a9 9 0 10-9 9m4.5-1.206a8.959 8.959 0 01-4.5 1.207"
							/>
						</svg>
					</div>
					<div className="flex flex-col justify-center">
						<h5 className="text-gray-800 uppercase dark:text-white mb-0 leading-5">
							Email
						</h5>
						<span>{contactInfo.email}</span>
					</div>
				</div>
				<div className="mb-5 flex space-x-5">
					<div className="h-14 w-14 rounded-full bg-primary text-white flex items-center justify-center flex-shrink-0">
						<svg
							xmlns="http://www.w3.org/2000/svg"
							className="h-6 w-6"
							fill="none"
							viewBox="0 0 24 24"
							stroke="currentColor">
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={1.5}
								d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
							/>
							<path
								strokeLinecap="round"
								strokeLinejoin="round"
								strokeWidth={1}
								d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
							/>
						</svg>
					</div>
					<div className="flex flex-col justify-center">
						<h5 className="text-gray-800 uppercase dark:text-white mb-0 leading-5">
							Address
						</h5>
						<span>{contactInfo.address}</span>
					</div>
				</div>
			</div>
		</div>
	);
}
